import React, { Fragment, memo } from 'react'
import type { FC, ReactNode } from 'react'
import { footerLinks, footerImages } from '@/assets/data/local_data'
import { AppFooterWrapper, FooterLeft, FooterRight } from './style'

interface IProps {
  children?: ReactNode
}

const AppFooter: FC<IProps> = () => {
  return (
    <AppFooterWrapper>
      <div className="content wrap-v2">
        <FooterLeft>
          <div className="links">
            {footerLinks.map((item, index) => {
              return (
                <Fragment key={item.title}>
                  <a href={item.link} target="_blank" rel="noopener noreferrer">
                    {item.title}
                  </a>
                  {index < footerLinks.length - 1 && (
                    <span className="line">|</span>
                  )}
                </Fragment>
              )
            })}
          </div>
          <div className="report">
            <span></span>
            <span>: 51jubao@service.netease.com</span>
          </div>
          <div className="copyright">
            <span>: 20220000120</span>
            <span>: B2-20150198</span>
            <a
              href="https://beian.miit.gov.cn/"
              rel="noopener noreferrer"
              target="_blank"
            >
              B2-20090191-18 ICP15006616-4
            </a>
            <a
              href="https://beian.miit.gov.cn/"
              rel="noopener noreferrer"
              target="_blank"
            >
              
            </a>
          </div>
          <div className="info">
            <span>1997-2025</span>
            <span>: [2024] 0900-042</span>
            <a
              href="https://www.beian.gov.cn/portal/registerSystemInfo?recordcode=33010802013307"
              rel="noopener noreferrer"
              target="_blank"
            >
              <i className="police-icon"></i>
              <span> 33010802013307</span>
            </a>
            <a
              href="https://www.beian.gov.cn/portal/registerSystemInfo"
              rel="noopener noreferrer"
              target="_blank"
            >
              
            </a>
          </div>
        </FooterLeft>
        <FooterRight>
          {footerImages.map((item) => {
            return (
              <li className="item" key={item.link}>
                <a
                  className="link"
                  href={item.link}
                  rel="noopener noreferrer"
                  target="_blank"
                ></a>
                <span className="title">{item.title}</span>
              </li>
            )
          })}
        </FooterRight>
      </div>
    </AppFooterWrapper>
  )
}

export default memo(AppFooter)
