import React, { memo, useEffect, useRef, useState } from 'react'
import type { FC, ReactNode } from 'react'
import {
  BarControl,
  BarOperator,
  BarPlayerInfo,
  PlayerBarWrapper
} from './style'
import { Slider } from 'antd'
import { Link } from 'react-router-dom'
import { shallowEqualApp, useAppDispatch, useAppSelector } from '@/store'
import { formatTime, getImageSize } from '@/utils/format'
import { shallowEqual } from 'react-redux'
import { getSongPlayUrl } from '@/utils/handle-player'
import { changeLyricIndexAction } from '../store/player'

interface IProps {
  children?: ReactNode
  show?: boolean
}

const AppPlayerBar: FC<IProps> = ({ show }) => {
  /**  */
  const [isPlaying, setIsPlaying] = useState(false)
  const [progress, setProgress] = useState(0)
  const [duration, setDuration] = useState(0)
  const [currentTime, setCurrentTime] = useState(0)
  const [isSliding, setIsSliding] = useState(false)
  const audioRef = useRef<HTMLAudioElement>(null)

  /** redux */
  const { currentSong, lyrics, lyricIndex } = useAppSelector(
    (state) => ({
      currentSong: state.player.currentSong,
      lyrics: state.player.lyrics,
      lyricIndex: state.player.lyricIndex
    }),
    shallowEqualApp
  )

  const dispatch = useAppDispatch()

  /**  */
  useEffect(() => {
    // 1.src
    if (!currentSong?.id) return
    audioRef.current!.src = getSongPlayUrl(currentSong.id)

    // 2., 
    if (isPlaying) {
      audioRef.current!.play().catch((err) => {
        console.log(':', err)
        setIsPlaying(false) // , 
      })
    }

    // 2.
    setDuration(currentSong.dt)
  }, [currentSong])

  /**  */
  function handleTimeUpdate() {
    // 1.
    const currentTime = audioRef.current!.currentTime * 1000

    // 2.
    if (!isSliding) {
      const progress = (currentTime / duration) * 100
      setProgress(progress)
      setCurrentTime(currentTime)
    }

    // 3.
    // currentTime/lyrics
    let index = lyrics.length - 1
    for (let i = 0; i < lyrics.length; i++) {
      const lyric = lyrics[i]
      if (lyric.time > currentTime) {
        index = i - 1
        break
      }
    }

    // 4.index
    if (lyricIndex === index || index === -1) return
    dispatch(changeLyricIndexAction(index))
  }

  /**  */
  function handlePlayBtnClick() {
    const audioEl = audioRef.current!

    // 
    if (!isPlaying) {
      audioEl
        .play()
        .then(() => {
          setIsPlaying(true)
        })
        .catch((err) => {
          console.log(':', err)
          setIsPlaying(false)
        })
    } else {
      audioEl.pause()
      setIsPlaying(false)
    }
  }

  function handleSliderChanging(value: number) {
    // 0.
    setIsSliding(true)

    // 1.progress
    setProgress(value)

    // 2.value
    const currentTime = (value / 100) * duration
    setCurrentTime(currentTime)
  }

  function handleSliderChanged(value: number) {
    // 1.
    const currentTime = (value / 100) * duration

    // 2.
    audioRef.current!.currentTime = currentTime / 1000

    // 3.currentTime/progress
    setCurrentTime(currentTime)
    setProgress(value)
    setIsSliding(false)
  }

  return (
    <PlayerBarWrapper className="sprite_playbar" $show={show}>
      <div className="content wrap-v2">
        <BarControl $isPlaying={isPlaying}>
          <button className="btn sprite_player prev"></button>
          <button
            className="btn sprite_player play"
            onClick={handlePlayBtnClick}
          ></button>
          <button className="btn sprite_player next"></button>
        </BarControl>

        <BarPlayerInfo>
          <Link to="/player">
            <img
              className="image"
              src={getImageSize(currentSong?.al?.picUrl, 50)}
              alt=""
            />
          </Link>
          <div className="info">
            <div className="song">
              <span className="song-name">{currentSong.name}</span>
              <span className="singer-name">{currentSong?.ar?.[0]?.name}</span>
            </div>
            <div className="progress">
              {/* Slider */}
              <Slider
                step={0.5}
                value={progress}
                tooltip={{ formatter: null }}
                onChange={handleSliderChanging}
                onChangeComplete={handleSliderChanged}
              />
              <div className="time">
                <span className="current">{formatTime(currentTime)}</span>
                <span className="divider">/</span>
                <span className="duration">{formatTime(duration)}</span>
              </div>
            </div>
          </div>
        </BarPlayerInfo>

        <BarOperator>
          <div className="left">
            <button className="btn pip"></button>
            <button className="btn sprite_playbar favor"></button>
            <button className="btn sprite_playbar share"></button>
          </div>
          <div className="right sprite_playbar">
            <button className="btn sprite_playbar volume"></button>
            <button className="btn sprite_playbar loop"></button>
            <button className="btn sprite_playbar playlist"></button>
          </div>
        </BarOperator>
      </div>
      <audio ref={audioRef} onTimeUpdate={handleTimeUpdate} />
    </PlayerBarWrapper>
  )
}

export default memo(AppPlayerBar)
