import styled from 'styled-components'

export const AppFooterWrapper = styled.div`
  height: 172px;
  background-color: #f2f2f2;
  color: #666;
  border-top: 1px solid #d3d3d3;
  font-size: 12px;

  .content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; /*  flex-start,  */
  }
`

export const FooterLeft = styled.div`
  padding-top: 15px;
  line-height: 24px;

  .links {
    display: flex;
    margin-bottom: 15px; /*  */

    a {
      color: #999;
      white-space: nowrap;
    }

    .line {
      margin: 0 10px;
      color: #c2c2c2;
    }
  }

  /*  */
  div:not(.links) {
    margin-top: 2px;
  }

  span,
  a {
    margin-right: 14px;
  }

  .police-icon {
    display: inline-block;
    width: 14px;
    height: 14px;
    background: url(${require('@/assets/img/sprite_footer_01.png')}) no-repeat;
    background-position: -1px -108px;
    vertical-align: middle;
    margin-right: 4px;
  }
`

export const FooterRight = styled.ul`
  display: flex;
  margin-top: 33px; /*  */

  .item {
    /*  flex  */
    text-align: center; /*  */
    width: 70px; /* item */

    .link {
      display: block; /*  */
      width: 50px;
      height: 45px;
      margin: 0 auto 10px; /*  */

      background-image: url(${require('@/assets/img/sprite_footer_02.png')});
      background-size: 110px 450px;
    }

    /*
      
       sprite_footer_02.png  background-position 
    */
    :nth-child(1) .link {
      background-position: -60px -101px;
    }
    :nth-child(2) .link {
      background-position: 0 0;
    }
    :nth-child(3) .link {
      background-position: -60px -50px;
    }
    :nth-child(4) .link {
      background-position: 0 -101px;
    }
    /* TODO:  5, 6, 7  background-position */
    :nth-child(5) .link {
      background-position: -60px 0;
    }
    :nth-child(6) .link {
      background-position: 0 -50px;
    }
    :nth-child(7) .link {
      background-position: -60px -101px; /*  */
    }

    .title {
      color: #666;
    }
  }
`
